@import url('https://fonts.googleapis.com/css2?family=Abel&family=Montserrat');

#skills {
    width: 100%;
    margin: auto;
    background: #FBBD71;
    padding: 20px 0px 40px 0px;
}
#skills h2 {
    font-family: 'Montserrat', sans-serif;
    padding : 10px 0px 10px 0
}

.tech-stack {
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 20px;
 

}

.tech-stackBx {
    width: 100%;
    padding: 25px 15px 25px 15px ;
    
    /* height: 100%; */
}

.tech-stackBx h2 {
    text-transform: uppercase;
    text-align : center;
}



.tech-stackIcon {
    width : 50%;
    margin: auto;
    display: grid;
    margin: auto;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(auto,auto);
    gap: 10px;
    /* margin : 20px 0 20px 0px; */
    /* border: 1px solid red; */
}


.tech-stackIcon  img {
    width: 120px;
    height: 120px;
    border: 10px solid black;
    padding: 15%;
    border-radius: 20px;
    transition: all .5s;
}

.tech-stackIcon  img:hover {
    transform: scale(0.8);
}

@media (min-width : 320px) and (max-width : 480px) {

    .tech-stack {
        width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 20px;
     
    
    }

    .tech-stackIcon  img {
        width: 100px;
        height: 100px;
        border: 5px solid black;
        padding: 8%;
        border-radius: 10px;
    }
    
    
}

@media (min-width : 481px) and (max-width : 768px) {

    .tech-stack {
        width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 20px;
     
    
    }

    .tech-stackIcon  img {
        width: 100px;
        height: 100px;
        border: 5px solid black;
        padding: 8%;
        border-radius: 10px;
    }
    
    
}


@media (min-width : 769px) and (max-width : 1023px) {

    .tech-stack {
        width: 100%;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }

    .tech-stackIcon  img {
        width: 100%;
        height: 100%;
        border: 2px solid black;
        padding: 8%;
        border-radius: 10px;
    }
    
    
}

@media (min-width : 1024px) and (max-width : 1200px) {

    .tech-stack {
        width: 100%;    
        margin: auto;
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 20px;
     
    
    }

    .tech-stackIcon  img {
        width: 80px;
        height: 80px;
        border: 5px solid black;
        padding: 8%;
        border-radius: 10px;
    }
    
    
}