@import url('https://fonts.googleapis.com/css2?family=Abel&family=Montserrat');



#project {
    padding: 5rem 0rem;
    margin : 5rem 0rem;
}



.main-projectSection h2 {
    padding: 0.5rem 0 0.5rem 0;
    color: #FBBD71;
    font-family: 'Montserrat', sans-serif;
    
}


.project-section {
    display: grid;
    row-gap: 20px;
    padding: 0 1rem 0 1rem;
}

.project-cards {
    padding: 0.8rem 0 0.8rem 0;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(1, auto);
    gap: 20px;
    margin-bottom: 20px;
}

.project-cards p {
    font-family: 'Abel', sans-serif;
    color: #FFF;
    font-size: 25px;
    text-align: left;
}

.project-cards .project-img {
    border-radius: 10px;
    width: 100%;
    
}

.project-cards .project-img img {
    border-radius: 10px;
    width: 100%;
    height: 100%;
}


.techstack-icon {
    width: 50%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.githubIcon {
    -webkit-filter: invert(100%);
    filter: invert(100%);
}

.btn-box a {
  background: none;
}

  .btn-box {
    font-family: 'Abel', sans-serif;
    width: 60%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .btn-grid .back .project-img {
    width: 12%;
    height : 12%;
     -webkit-filter: invert(60%); /* Safari/Chrome */
     filter: invert(60%);
  }

  .btn-box .btn-grid {
    position: relative;
    overflow: hidden;
    transition: filter 0.2s;
  }

  .btn-box button {
    font-weight: bold;
  }
  
  .btn-box button > * {
    display: inline-block;
    width: 100%;
    transition: transform 0.4s ease-in-out;
  }
  
  .btn-box button .back {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-110%, -50%);
  }
  
  .btn-box button:enabled:hover .back,
  .btn-box button:focus .back {
    transform: translate(-50%, -50%);
  }
  
  .btn-box button:enabled:hover .front,
  .btn-box button:focus .front {
    transform: translateX(110%);
  }


.btn-box button:enabled:hover,
.btn-box button:focus {
  transform: scale(1.02);
}


.btn-box button {
  width: 100%;
  line-height: 1.5;
  padding: 15px 10px;
  border: 1px solid var(--borderFormEls);
  border-radius: 10px;
  color: var(--white);
  background: var(--bgFormEls);
  transition: background-color 0.3s cubic-bezier(0.57, 0.21, 0.69, 1.25),
    transform 0.3s cubic-bezier(0.57, 0.21, 0.69, 1.25);
}

.btn-box .btn-grid {
  grid-template-columns: auto auto auto;
  align-items: center;
}

.btn-box {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 1.5rem;
}



