.contact-btn {
    display: flex;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
    position : fixed;
    bottom: 0;
    padding: 1rem;

    
}

.fa {
    font-size: 40px;
    background-color: white;
    padding: 0.4rem;
    border-radius: 50%;
}

.button {
    display: flex;
    flex-direction: column;
    gap: 20px;


}

.fa :hover {
    border : 1px solid black;
    color : black;
    background: transparent;
    cursor: pointer;
}

.contact-btn a {
    background: none;
}
