@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital@1&display=swap');



.home {
    color: white;
    font-family: 'Inconsolata', monospace;
}

.border-img {
    width: 350px;
    height: 350px;
    background: -webkit-linear-gradient(360deg, #383836 10%, #4a4a4a 360%);
    background: linear-gradient(360deg, #383836 10%, #4a4a4a 360%);
    border-radius: 34% 66% 32% 68% / 39% 59% 41% 61%;
    margin-top: 10rem;
    position: absolute;
    left: 11rem;
    transition: transform .5s;
    z-index: 2;

}

.wave {
    animation-name: wave-animation;
    /* Refers to the name of your @keyframes element below */
    animation-duration: 2.5s;
    /* Change to speed up or slow down */
    animation-iteration-count: infinite;
    /* Never stop waving :) */
    transform-origin: 70% 70%;
    /* Pivot around the bottom-left palm */
    display: inline-block;
}

@keyframes wave-animation {
    0% {
        transform: rotate(0.0deg)
    }

    10% {
        transform: rotate(14.0deg)
    }

    /* The following five values can be played with to make the waving more or less extreme */
    20% {
        transform: rotate(-8.0deg)
    }

    30% {
        transform: rotate(14.0deg)
    }

    40% {
        transform: rotate(-4.0deg)
    }

    50% {
        transform: rotate(10.0deg)
    }

    60% {
        transform: rotate(0.0deg)
    }

    /* Reset for the last half to pause */
    100% {
        transform: rotate(0.0deg)
    }
}

.home-page {
    display: flex;
    width: 100%;
}

.change-text {
    color: #FBBD71;
}

.intro {
    margin-top: 13rem;
    margin-left: 45rem;
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: left;
    overflow: hidden;

}

.name {
    font-size: 3rem;
}


.resume-btn a {
    background : none;
    
}



.border-img img {
    width: 80%;
    margin: auto;
    position: relative;
    bottom: 6rem;
    /* -webkit-filter: grayscale(100%); */
    /* filter: grayscale(100%); */
    border-radius: 34% 66% 32% 68% / 39% 59% 41% 61%;
    transition: transform .2s;
}

.border-img :hover {
    transform: scale(1.1);
}

.slide-container .text {
    position: relative;
    color: #FBBD71;
    font-size: 30px;
    font-weight: 600;
}

.slide-container .text.first-text {
    color: #fff;
    margin-right: 10px;
}

.text.sec-text::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    border-left: 2px solid #FBBD71;
    animation: animate 6s infinite;
}


  .resume-btn .btn-grid {
    position: relative;
    overflow: hidden;
    transition: filter 0.2s;
  }

  .resume-btn button {
    font-weight: bold;
  }
  
  .resume-btn button > * {
    display: inline-block;
    width: 100%;
    transition: transform 0.4s ease-in-out;
  }
  
  .resume-btn button .back {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-110%, -50%);
  }



.resume-btn button {
  width: 100%;
  line-height: 1.5;
  padding: 15px 5px;
  border: 1px solid var(--borderFormEls);
  border-radius: 10px;
  color: black;
  background: #fff;
  transition: background-color 0.3s cubic-bezier(0.57, 0.21, 0.69, 1.25),
  transform 0.3s cubic-bezier(0.57, 0.21, 0.69, 1.25);
  margin-top: 1.5rem;
  font-weight: bold;
}

.resume-btn button:hover {
    background: none;
    border: 3px solid #fff;
    color: white;
    transition: .9s;

}

.resume-btn .btn-grid {
  grid-template-columns: auto auto auto;
  align-items: center;
}

.resume-btn {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 1.5rem;
}



@keyframes animate {

    40%,
    60% {
        left: 100%;
    }

    100% {
        left: 0%;
    }
}



