@import url('https://fonts.googleapis.com/css2?family=Abel');

.img-logo {
  width: 22%;
}

.logo {
    width: 100%;
    -webkit-filter: invert(100%); /* Safari/Chrome */
    filter: invert(100%);
}

nav {
    padding: 20px 5px;
    font-family: 'Abel', sans-serif;
    background: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 3;
    
    
}
ul {
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    
}

ul li{
    text-decoration: none;
}

nav .nav-section {
    float: right;
    margin-right: 20px  ;
}

nav .nav-section li {
    display: inline-block;
    /* line-height: 80px; */
    margin: 0px 20px;
}

nav .nav-section li a {
    color: white;
    font-size: 17px;
    text-transform: uppercase;
    padding: 7px 8px;
    border-radius: 3px;
    text-decoration: none;
}

a:hover {
    background: #FBBD71;
    transition: 0.5s;
    color: #000;
}

/* Hamburger button */

    /* The relevant bit starts here */
    .checkbtn {
      width: 40px;
      height: 40px;
      border: none;
      cursor: pointer;
      background-color: transparent;
      float: right;
      line-height: 80px;
      margin-right: 40px;
      display: none;
     
      
      
    }

    /* #check {
        display: none;
    } */
    
    .hamburger-line {
      display: block;
      height: 4px;
      border-radius: 4px;
      background-color: white;
      transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.1s ease-out;
      
    }
    
    .hamburger-line:not(:first-child) {
      margin-top: 7px;
    }
    
    /* To properly translate the element, you should input inside the translateY the sum of the height of your line + margin between 2 lines. In this case, 4px height + 7px margin = 11px */
    .hamburger.active .hamburger-line-1 {
      transform: translateY(11px) rotate(-135deg);
    }
    
    .hamburger.active .hamburger-line-2 {
      opacity: 0;
      transform: translateX(-50%);
    }
    
    /* To properly translate the element, you should input inside the translateY the sum of the height of your line + margin between 2 lines. In this case, 4px height + 7px margin = 11px */
    .hamburger.active .hamburger-line-3 {
      transform: translateY(-11px) rotate(-225deg);
    }

    @media (min-width : 320px) and (max-width : 480px) {

        .img-logo {
            width: 100%;
          }
          .checkbtn {
            margin-right: 10px;
          }
        
      }

      @media (min-width : 481px) and (max-width : 768px) {
        .img-logo {
            width: 80%;
          }
          .checkbtn {
            margin-right: 10px;
          } 
      }

      
      @media (min-width : 1024px) and (max-width : 1200px) {
        
        .img-logo {
          width: 40%;
        }
        ul {
          width: 70%;
          margin: auto;
        }

       
        
      }

   

    @media (max-width : 920px){

        .checkbtn {
            display: block;
        }

        .nav-section {
            position: fixed;
            width: 100%;
            margin: auto;
            text-decoration: none;
            height: 100%;
            background: #000;
            top: -110%;
            left: 0;
            text-align: center;
            transition: all .5s;
            
        }
        

        nav .nav-section li {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            margin-top: 1rem;
            
        }

        nav .nav-section li a {
            font-size: 20px;
            
             
        }

        a:hover , a.active {
            background: none;
            color: #FBBD71;
        }

        
        
        
    } 