@import url('https://fonts.googleapis.com/css2?family=Abel&family=Montserrat');



#aboutMe {
    padding: 1rem 0rem;
}
.about-section {
    width : 100%;
    height : auto;
    background-color: #FBBD71;
    margin-top: 13rem;
    font-family: 'Montserrat', sans-serif;
    padding: 15px 0px 15px 0px;
}
    
.about-me {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 3rem;
    font-family: 'Abel', sans-serif;
    text-align: left;
}


.aboutme-para p {
    font-size: 25px;
}

.about-gif {
    width: 100%;
    padding: 0rem 2rem;
    margin: 0rem 2rem;
}

.about-gif img {
    width: 100%;
    height: 100%;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}


@media (min-width : 320px) and (max-width : 480px) {

    .about-section {
        margin-top: 30rem;
    }

    .about-me {
        display: block;
    }
    .aboutme-para p {
        width: 90%;
        margin: auto;
        font-size: 20px;
    }
    .About {
        font-size: 20px;
    }
    .about-gif img {
        display: none;
    }
     
     
 }
 
