@import url('https://fonts.googleapis.com/css2?family=Abel&family=Montserrat');

.wakatime-gitub-states {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 1rem 0rem;
    margin: 1rem 0rem;
    

}

.github-calender-section {
    background-color: #FBBD71;
    padding: 1rem 0;
}
.github-states-section {
    padding: 1rem 0rem;
}


.github-states-section h2 , .github-calender-section h2 {
    padding: 2rem 0 1rem 0;
    color: #FBBD71;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;

}

.github-calender-section h2 {
    color: black;
}


.github-states {
    padding: 1rem 0rem;
    margin: 1rem 0rem;
}



@media (max-width : 960px) {
    .wakatime-gitub-states {
        flex-direction: column;
    }
    
}

@media (max-width : 768px) {
    .github-states-img {
        width: 100%;
        margin: auto;
        padding: 0rem 0.5rem;
    }
}
